<template>
  <div id="login">
    <div class="model-mask">
      <div class="model-wrapper">
        <div class="model-container">
          <div class="main">
            <img src="../assets/images/note.jpg" alt="note">
          </div>
          <div class="form">
            <h3 @click="showRegister">创建账户</h3>
            <div v-show="isShowRegister" class="register">
              <!--              v-model将用户名存储到register.username里面-->
              <input type="text" v-model="register.username" placeholder="用户名">
              <input type="password" v-model="register.password" placeholder="密码">
              <!--               register.Error存在便会有这个类名且显示-->
              <p :class="{error: register.isError}">{{ register.notice }}</p>
              <div class="button" @click="onRegister">创建账号</div>
            </div>
            <h3 @click="showLogin">登录</h3>
            <div v-show="isShowLogin" class="login">
              <input type="text" v-model="login.username" placeholder="输入用户名">
              <input type="password" v-model="login.password" placeholder="密码">
              <p :class="{error:login.isError}">{{ login.notice }}</p>
              <div class="button" @click="onLogin">登录</div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {mapActions} from 'vuex';
//判断登录状态
// request('/auth')
//   .then(data => {
//     console.log(data);
//   });
// Auth.getInfo().then(data => {
//   console.log(data);
// });
export default {
  name: 'Login',
  data() {
    return {
      //是否展示登录或者注册页面
      isShowLogin: true,
      isShowRegister: false,
      //登录信息
      login: {
        username: '',
        password: '',
        notice: '请输入用户名和密码',        //如果出现错误会显示notice内容
        isError: false
      },
      //注册信息
      register: {
        username: '',
        password: '',
        notice: '创建账号后，请记住用户名和密码',        //如果出现错误会显示notice内容
        isError: false
      }
    };
  },
  methods: {
    ...mapActions({
      loginUser: 'login',
      registerUser: 'register'
    }),
    //显示登录页面
    showLogin() {
      this.isShowLogin = true;
      this.isShowRegister = false;
    },
    //显示注册页面
    showRegister() {
      this.isShowLogin = false;
      this.isShowRegister = true;
    },
    //检查注册用户名和密码是否合法
    onRegister() {
      //如果用户输入的用户名不匹配注册规范，便会弹出错误
      if (!/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.register.username)) {
        this.register.isError = true;
        this.register.notice = '用户名必须是3~15个字符，仅限于字母数字下划线中文';
      }
      if (!/^.{6,16}$/.test(this.register.password)) {
        this.register.isError = true;
        this.register.notice = '密码长度为6~16个字符';
      }
      this.registerUser({
        username: this.register.username,
        password: this.register.password
      })
        .then(() => {
          //自动跳转到笔记列表页面
          this.register.isError = false;
          this.register.notice = '';
          this.$router.push({path: 'notebooks'});
        }).catch(data => {
        this.register.isError = true;
        this.register.notice = data.msg;
      });

    },
    onLogin() {
      if (/^[\w\u4e00-\u9fa5]{3,15}$/.test(this.login.username) === false) {
        this.login.isError = true;
        this.login.notice = '用户名3~15个字符，仅限于字母数字下划线中文';
      }
      if (!/^.{6,16}$/.test(this.login.password)) {
        this.login.isError = true;
        this.login.notice = '密码长度为6~16个字符';
      }

      this.loginUser({
        username: this.login.username,
        password: this.login.password
      })
        .then(() => {
          //自动跳转到笔记列表页面
          this.login.isError = false;
          this.login.notice = '';
          this.$router.push({path: 'notebooks'});
        }).catch(data => {
        this.login.isError = true;
        this.login.notice = data.msg;
      });
    }
  }
};
</script>

<style lang="less" scoped>
.model-mask {

  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .7);
  display: table;
  //opacity 不透明度
  transition: opacity .3s ease;
}

.model-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.model-container {
  width: 800px;
  height: 500px;
  margin: 0 auto;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
  transition: all .3s ease;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  display: flex;


  .main {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 90%;
      border-radius: 10px;
    }

    flex: 1;
    /*登录界面图片*/
    background-color: #36bc64;
    background-size: contain;
  }

  .form {
    width: 270px;
    border-left: 1px solid #ccc;

    h3 {
      padding: 10px 20px;
      font-weight: normal;
      font-size: 16px;
      border-top: 1px solid #eee;
      cursor: pointer;

      &:nth-of-type(2) {
        //兄弟节点的第一个兄弟
        border-bottom: 1px solid #eeeeee;
      }
    }

    .button {
      background-color: #2bb964;
      height: 36px;
      line-height: 36px;
      text-align: center;
      font-weight: bold;
      color: #fff;
      border-radius: 4px;
      margin-top: 18px;
      cursor: pointer;
    }

    .login, .register {
      padding: 10px 20px;
      border-top: 1px solid #eeeeee;


      input {
        display: block;
        width: 100%;
        height: 35px;
        line-height: 35px;
        padding: 0 6px;
        border-radius: 4px;
        border: 1px solid #cccccc;
        outline: none;
        font-size: 14px;
        margin-top: 10px;
      }

      //获得焦点时（点击文本框）便会响应
      input:focus {
        border: 3px solid #9dcaf8;
      }

      p {
        font-size: 12px;
        margin-top: 10px;
        color: #444;
      }

      .error {
        color: red;
      }
    }

    .login {
      border-top: 0;
    }
  }
}


</style>



